.img-thumbnail {
    background: #fff;
    border-radius: 2px;
    height: 325px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  

  .img-thumbnail > .img-container > img {
    width: 100%;
    height: 100%;
  }
  
  .img-thumbnail > .remove {
    color: #ff1744;
    font-size: 48px;
    position: absolute;
    top: -20px;
    right: 5px;
    cursor: pointer;
  }
  
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
  
.title {
    width: 100%;
    text-align: center;
    font-size: 3rem;
    text-decoration: underline;
    color: #2A77AE;
    margin-bottom: 50px;
  }
  
